@import url(https://fonts.googleapis.com/css?family=Press+Start+2P&display=swap);
*{margin:0;padding:0;box-sizing:border-box}html{font-size:62.5%}body{font-family:"Press Start 2P", sans-serif;color:#eee;font-size:62.5%}main{height:65vh;color:#eee;display:flex;align-items:center;justify-content:space-around;background-color:#222}.correct{background-color:#60b347}.left{width:52rem;display:flex;flex-direction:column;align-items:center}.right{width:52rem;font-size:2rem}.guess{background:none;border:4px solid #eee;font-family:inherit;color:inherit;font-size:5rem;padding:2.5rem;width:25rem;text-align:center;display:block;margin-bottom:3rem}.btn{border:none;background-color:#eee;color:#222;font-size:2rem;font-family:inherit;padding:2rem 3rem;cursor:pointer}.btn:hover{background-color:#ccc}.message{margin-bottom:8rem;height:3rem}.label-score{margin-bottom:2rem}header{position:relative;height:35vh;border-bottom:7px solid #eee;background-color:#222}h1{font-size:4rem;text-align:center;position:absolute;width:100%;top:45%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.number{background:#eee;color:#333;font-size:6rem;width:15rem;padding:3rem 0rem;text-align:center;position:absolute;top:35%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%)}.correctNum{width:25rem;padding:5rem 0rem}.between{font-size:3rem;position:absolute;top:3rem;right:3rem}.again{position:absolute;top:2rem;left:2rem}

